html * {
  outline: 0px solid red;
}

label {
  display: block;
  margin: 10px;
}
input[type="radio"] {
  display: none;
}

.radio-button {
  position: relative;
  padding-left: 20px;
  height: 15px;
}

.radio-button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 15px;
  height: 15px;
  border: 2px solid blue;
  border-radius: 50%;
}

@keyframes jump {
  0% {
    transform: scale(0);
    opacity: 0;
    background-color: lightgreen;
  }

  50% {
    transform: scale(1.5);
    opacity: 0.5;
    background-color: grey;
  }

  100% {
    transform: scale(1);
    opacity: 1;
    background-color: blue;
  }

}

.radio-button:after {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  display: block;
  width: 9px;
  height: 9px;
  background-color: blue;
  border-radius: 50%;
  transform: scale(0);
}

input[type="radio"]:checked + .radio-button:after {
  animation: jump .5s ease forwards;
}

input[type="radio"]:checked + .radio-button {
  color: red;
}